import "../assets/css/property.css";
import "../assets/css/property.min.css";
import "../assets/css/archive-property.min.css";
import { useState, useEffect, useContext } from "react";
import MetaTags from "react-meta-tags";
import Footer from "../components/footer";

import NavigationBar from "../components/HomeComponents/topbar/navigationbar";
import LanguageBar from "../components/HomeComponents/topbar/languagebar";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import pagetitle from "../assets/img/page-title.webp";
import { MainContext } from "../components/contexts/MainContext";

const Compare = () => {

  const navigate = useNavigate();

  const { type: paramProp } = useParams();

  const {
    compare,
  } = useContext(MainContext);


  const [properties, setProperties] = useState([]);

  const [loading, setLoading] = useState(true);


  useEffect(() => {
    (async () => {
      try {
        const {
          data: { properties: pties },
        } = await axios.get(
          `/api/qsyrys/properties/compare`,
          {
            params: {
              compare: compare.map(e => e.id).join(',')
            },
          }
        );

        setLoading(false);

        console.log(pties, 'ss')


        setProperties(pties);

      } catch (err) {
        console.log(err, 'ss')

        navigate("/404");
      }
    })();
  }, []);


  return loading ? (
    <></>
  ) : (
    <>
      <MetaTags>
        <title>
          Compare Properties | Property
          Investment
        </title>
      </MetaTags>

      <div
        className="home page-template page-template-elementor_header_footer elementor-kit-4695 page page-id-58 wp-embed-responsive beyot-class chrome header-is-float header-sticky-fix-style elementor-default elementor-template-full-width elementor-kit-4695 elementor-page elementor-page-58 e--ua-blink e--ua-chrome e--ua-webkit elementor elementor-58"
        id="wrapper"
      >
        <LanguageBar />
        <NavigationBar />

        <div id="wrapper-content" className="clearfix ">
          <section className="page-title page-title-background py-[120px]">
            <div
              className="page-title-background page-title-parallax"
              style={{
                backgroundImage: `url(${pagetitle})`,
              }}
            ></div>
            <div className="vc_row-background-overlay bg-[rgba(0,0,0,0.68)]"></div>
            <div className="container">
              <div className="page-title-inner">
                <div className="page-title-main-info">
                  <h1>Compare</h1>
                </div>

                <ul className="breadcrumbs">
                  <li>
                    <Link to="/" className="home">
                      Home
                    </Link>
                  </li>
                  <li>
                    <span className="capitalize">
                      compare
                    </span>
                  </li>{" "}
                </ul>
              </div>
            </div>
          </section>
          <div
            id="primary-content"
            className="pd-top-100 pd-bottom-100 sm-pd-top-80 sm-pd-bottom-80"
          >
            <div className="container clearfix">
              <div className="row">
                <div className="col-lg-9 order-lg-2 archive-property-inner">
                  <div id="container">
                    <div id="content" role="main">
                      <link
                        rel="stylesheet"
                        id="ere_property-css"
                        href="https://qsyrys.com/wp-content/plugins/essential-real-estate/public/templates/shortcodes/property/assets/css/property.min.css"
                        type="text/css"
                        media="all"
                      />
                      <link
                        rel="stylesheet"
                        id="ere_archive-property-css"
                        href="https://qsyrys.com/wp-content/plugins/essential-real-estate/public/assets/css/archive-property.min.css"
                        type="text/css"
                        media="all"
                      />

                     {properties.length < 2 ? (
                        "No items to compare"
                     ) : (
                        <></>
                     )}


                    </div>
                  </div>
                </div>


              </div>
            </div>
          </div>
        </div>

        <div className="overlay-canvas-sidebar"></div>

        <Footer />
      </div>
    </>
  );

};

export default Compare;
